import styled from "styled-components";

export const WrapperSignIn = styled.div`
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	margin: 50px 0 50px 0;
	color: rgb(255, 255, 255);
`;

export const RegIconWrap = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgb(171, 44, 142);
	width: 30px;
	height: 30px;
	border-radius: 50%;
`;

export const RegIcon = styled.img`
	width: 60%;
	height: 60%;
`;

export const SignInStatus = styled.h2`
	font-size: 16px;
	font-weight: 400;
	letter-spacing: 0.8px;
`;

export const WrapperForm = styled.div`
	display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 5px;

    input  {
        width: 45%;
        background-color: rgb(14, 14, 14);
        border: 1.5px solid rgba(50,50,51,.8);
        border-radius: 5px;
        padding: 8px 5px 8px 5px;
        color: rgb(255, 255, 255);
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        letter-spacing: 0.8px;
        &:focus {
            outline-color: rgba(50,50,51,.8);
        }
    }

    .email {
        width: 100%;
    }


`;


